import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select} from "antd";

const { Option } = Select;
const { TextArea } = Input;
//const InputGroup = Input.Group;

class SizeCreateForm extends Component {


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { size } = this.props;
    const { loading, units } = size;
    const error = size.errorField ? size.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}> 
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="sizes.add.code" />}
              validateStatus={
                error.size_code || getFieldError("size_code")
                  ? "error"
                  : null
              }
              help={
                error.size_code
                  ? error.size_code
                  : getFieldError("size_code")
              }
            >
              {getFieldDecorator("size_code", {
                //initialValue: data.size_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.name" />}
            >
              {getFieldDecorator("size_name", {
                //initialValue: data.size_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="units.pageHeader" />}
            >
              {getFieldDecorator("id_unit", {
                //initialValue: 0,
              })(
                <Select
                  loading={loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //disabled={used ? true : false}
                >
                  {units.map((data, key) => (
                    <Option key={key} value={data.id_unit}>
                      { data.unit_code + " (" + data.unit_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
              })(<TextArea />)}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedSizeCreateForm = Form.create({ name: "create_size" })(SizeCreateForm);

export default WrappedSizeCreateForm;
